import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpModule, JsonpModule } from '@angular/http';
import { OrderProductComponent } from './order-product.component'
import { RouterModule } from '@angular/router';
import { ServiceShared } from '../shared/service.module'

import { TopbarSharedModule } from '../shared/topbar.module';

import { PromotionComponent } from './promotion/promotion.component';
import { OrderProductRoutingModule } from './order-product-routing.module';
import { PromotionCardComponent } from './shared/promotion-card/promotion-card.component';
import { ProductFilterComponent } from './product-filter/product-filter.component';
import { FilterItemComponent } from './shared/filter-item/filter-item.component'
import { OrderProductService } from './service/order-product.service'
import { PromotionService } from './service/promotion.service';
import { ProductListComponent } from './product-list/product-list.component';
import { ProductCardComponent } from './shared/product-card/product-card.component';
import { ProductCardItemComponent } from './shared/product-card-item/product-card-item.component';
import { ProductMenuComponent } from './shared/product-menu/product-menu.component';
import { ProductMenuFilterComponent } from './shared/product-menu-filter/product-menu-filter.component';
import { ProductListService } from './service/product-list.service';
import { ProductDetailComponent } from './product-detail/product-detail.component';
import { ProductCardDetailComponent } from './shared/product-card-detail/product-card-detail.component';
import { ProductCartDetailItemComponent } from './shared/product-card-detail/product-card-detail-item/product-card-detail-item.component';
import { ProductTradeComponent } from './product-trade/product-trade.component';
import { ProductTradeCardComponent } from './shared/product-trade-card/product-trade-card.component';
import { ProductTradeCalComponent } from './product-trade-cal/product-trade-cal.component';
import { ProductTradeDetailCardComponent } from './shared/product-trade-detail-card/product-trade-detail-card.component'
import { ProductTradeFreeCardComponent } from './shared/product-trade-free-card/product-trade-free-card.component'
import { ProductTradeListService } from './service/product-trade-list.service';
import { ProductCardTradeItemComponent } from './shared/product-card-trade-item/product-card-trade-item.component';
import { CalTradeService } from './service/cal-trade.service';
import { ProductItemService } from './service/product-item.service';
import { PromotionProductComponent } from './promotion-product/promotion-product.component'
@NgModule({
  imports: [
    CommonModule,
    OrderProductRoutingModule,
    FormsModule,
    RouterModule,
    HttpModule,
    JsonpModule,
    ServiceShared,
    TopbarSharedModule
  ],
  declarations: [
    PromotionComponent,
    OrderProductComponent, 
    PromotionCardComponent, 
    ProductFilterComponent, 
    FilterItemComponent,
    ProductListComponent,
    ProductCardComponent,
    ProductMenuComponent,
    ProductMenuFilterComponent,
    ProductCardItemComponent,
    ProductCardTradeItemComponent,
    ProductDetailComponent,
    ProductCardDetailComponent,
    ProductCartDetailItemComponent,
    ProductTradeComponent,
    ProductTradeCardComponent,
    ProductTradeCalComponent,
    ProductTradeDetailCardComponent,
    ProductTradeFreeCardComponent,
    PromotionProductComponent
  ],
  providers:[
    OrderProductService, 
    PromotionService, 
    ProductListService, 
    ProductTradeListService,
    CalTradeService,
    ProductItemService
  ],
  exports:[ProductCardDetailComponent, ProductCartDetailItemComponent]
})
export class OrderProductModule { }



