import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ProductItemService } from '../../service/product-item.service';

@Component({
  selector: 'app-product-card-trade-item',
  templateUrl: './product-card-trade-item.component.html',
  styleUrls: ['./product-card-trade-item.component.scss']
})
export class ProductCardTradeItemComponent implements OnInit {
  @Input() item: any
  @Input() color: string = 'RED'
  @Input() date: string
  @Input() endDate: string = "-"
  @Input() quotaFlag: string;
  @Input() quotaQty: number;  
  @Input() colorShow: boolean = false
  @Output() itemSelect: EventEmitter<any> = new EventEmitter<any>()

  public colorHex: string = '#FF0000'
  public quotaString = 'Free'
  constructor(
    private productItem: ProductItemService
  ) {
  }

  ngOnInit() {
    if (this.color != undefined) {
      this.colorHex = this.productItem.colourNameToHex(this.color)
    }

    if (this.quotaFlag === "Y") {
      this.quotaString = this.quotaQty.toString()
    }

    if (this.endDate === undefined) {
      this.endDate = "-"
    }
  }

  tradeSelect() {
    this.itemSelect.emit(this.item)
  }
}



