import { Component, OnInit,Input, Output, EventEmitter } from '@angular/core';
import { ProductTradeListService } from '../../service/product-trade-list.service';

@Component({
  selector: 'app-product-trade-card',
  templateUrl: './product-trade-card.component.html',
  styleUrls: ['./product-trade-card.component.scss']
})
export class ProductTradeCardComponent implements OnInit {
  @Input() product: any;
  @Input() productItems: any;
  @Input() colorShow: boolean = true
  productName: string;
  productPrice: string;
  items: any;
  @Output() updateProductTrade: EventEmitter<any> = new EventEmitter();

  constructor(
    private productTradeListService: ProductTradeListService
  ) {
  }

  ngOnInit() {
    this.productName = this.product[0].productDescription
    this.productPrice = this.product[0].priceExcAmt
    this.items = this.product[0].listProductDetail
  }

  onChangeQty($event){
    this.items.map((value, index) => {
      if(value.productId === $event.productId){
        this.items[index] = $event
      }
    })
    this.updateProductTrade.emit(this.items)
  }

}



