import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductTradeListService } from '../service/product-trade-list.service';
import { PromotionService } from '../service/promotion.service';

@Component({
  selector: 'app-product-trade',
  templateUrl: './product-trade.component.html',
  styleUrls: ['./product-trade.component.scss']
})
export class ProductTradeComponent implements OnInit {
  getProductTradeListPromise: any;
  active : boolean = false
  detail: string = "";
  productList: any = [];
  public colorShow: boolean = true;

  constructor(
    private router : Router,
    private productTradeListService: ProductTradeListService,
    private promotionService: PromotionService
  ) { }

  ngOnInit() {
    $("#preload").css("display", "block");
    this.getProductTradeListPromise = this.productTradeListService.getList();
    this.getProductTradeListPromise.then((res: any) => {
      $("#preload").css("display", "none");
      if (res.resultCode == "S"){
        this.detail = res.tradeDesc;
        this.productList = res.listMainProduct;
        this.colorShow = this.productTradeListService.isColorShow()
      }
    }).catch((e: any) => {
     console.log(e)
    })
  }

  onFilter() {
    console.log("filter")
  }

  onSort() {
    console.log("sort")
  }

  readmore() {
    this.active = !this.active
  }

  tradeCalculate() {
    this.router.navigate(['/order-product/product-trade-cal'])
  }

  onUpdateProductTrade($event){
    this.productTradeListService.setListProductBuy(this.productList)
  }
}



