import { Component, OnInit } from "@angular/core";
import { Http, Response } from "@angular/http";
import { Router } from "@angular/router";
import { ProductListService } from "../service/product-list.service";

@Component({
  selector: "app-product-list",
  templateUrl: "./product-list.component.html",
  styleUrls: ["./product-list.component.scss"]
})
export class ProductListComponent implements OnInit {
  private result: any;
  public colorShow: boolean = true;
  public productList: any = [];
  private getProductListPromise: any;
  public showModal: boolean = false;
  public showFilter: boolean = false;
  public showSort: boolean = false;
  public productExist: boolean = true;

  constructor(
    private router: Router,
    private productListService: ProductListService
  ) {}

  ngOnInit() {
    $("#preload").css("display", "block");
    this.getProductListPromise = this.productListService.getProductList();
    this.getProductListPromise
      .then((res: any) => {
        $("#preload").css("display", "none");
        if (res.resultCode == "S") {
          this.productExist = true;
          this.productList = res.listProductCatalog;
          this.colorShow = this.productListService.isColorShow()
        } else {
          this.productExist = false;
          this.productList = [];
        }
      })
      .catch((e: any) => {
        console.log(e);
      });
  }

  onFilter() {
    this.showModal = true;
    this.showFilter = true;
    this.showSort = false;
  }

  onSort() {
    this.showModal = true;
    this.showFilter = false;
    this.showSort = true;
  }

  onSubmitFilter() {
    this.showModal = false;
    this.showFilter = false;
    this.showSort = false;
  }

  onClickProductDetail(product) {
    this.router.navigate(["/order-product/product-detail"]);
  }

  onSelectProductTrade($event) {
    console.log($event)
    this.router.navigate(["/order-product/promotion-product"]);
  }
}



