import { Component, OnInit,Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-product-menu-filter',
  templateUrl: './product-menu-filter.component.html',
  styleUrls: ['./product-menu-filter.component.scss']
})
export class ProductMenuFilterComponent implements OnInit {
  @Input() showModalPopup: boolean;
  @Input() showFilter: boolean;
  @Input() showSort: boolean;
  @Output() submitFilter = new EventEmitter()

  public filterLists: any = []
  public sortLists: any = []
  
  constructor() {
  }

  ngOnInit() {
    this.getFilterList()
    this.getSortlist()
  }

  private getFilterList() {
    this.filterLists = [
      {
        name:'สินค้าโควต้า (Quota)',
        selected:false,
        tagName:'QUOTA'
      },
      {
        name:'สินค้าฟรีโควต้า (Free Quota)',
        selected:false,
        tagName:'FREEQUOTA'
      },
      {
        name:'สินค้าโปรโมชั่น (Trade)',
        selected:false,
        tagName:'TRADE'
      },
      {
        name:'ของแถม (Free Goods)',
        selected:false,
        tagName:'FREEGOODS'
      },
      {
        name:'ทั้งหมด (All)',
        selected:false,
        tagName:'ALL'
      }
    ]
  }

  private getSortlist() {
    this.sortLists = [
      {
        name:'วันสิ้นสุดโควต้า (Quota End Date)',
        selected:false,
        tagName:'ENDDATE'
      },
      {
        name:'จำนวนโควต้าที่ได้รับ (Quota Qty.)',
        selected:false,
        tagName:'QTY'
      },
      {
        name:'Brand / Model',
        selected:false,
        tagName:'BRAND'
      }
    ]
  }

  public submit() {
    this.submitFilter.emit()
  }

  private clearSelectList() {
    this.filterLists.map(function(obj,i){
      obj.selected = false
    })
    this.sortLists.map(function(obj,i){
      obj.selected = false
    })
  }

  public selectFilter(filter){
    this.clearSelectList()
    filter.selected = true
  }

  public selectSort(sort){
    this.clearSelectList()
    sort.selected = true
  }
  
}



