import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';
import { Observable } from 'rxjs/Observable';
import { UserInfo } from '../../services/userInfo'


@Injectable()
export class OrderProductService {
  private categoryList : any=[];
  private selectedProduct: any=[];
  private type: string;
  private payment: string;
  private nonExistColorList: any = ['SIM', 'CARD', 'ROM', 'SIM-PREPAID', 'SIM-POSTPAID']

  constructor(
    private http: Http,
    private authHttpService : AuthHttpService,
    private service : UserInfo,
  ) { }

  getCategoryName(): Promise<any> {    
    let body = {
      'listCompany': this.service.getCompanyList(),
      'distChannel' : this.service.getDistChannel(),
      'customerGroup' : this.service.getCusGroup(),
      'dealerCode': this.service.getDealerCode(),
      'temp' : this.service.gToken(this.service.getUsername()),
      'temp_' : this.service.getAccessToken()
    };
    
    return this.authHttpService.post('getCategoryName',
        body).toPromise()
            .then(this.extractData)
            .catch((error : Response | any) => {
                $("#preload").css("display", "none");
                let errMsg: string;
                if (error instanceof Response){
                    const body = error.json() || '';
                    const err = body.error || JSON.stringify(body);
                    errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
                }else{
                    errMsg = error.message ? error.message : error.toString();
                }
                return Observable.throw(errMsg);
    });
  }
  
  private extractData(res){
    return res;
  }
  
  private handleError(error: Response | any) {
      if (typeof error === 'object'&& error.toString().indexOf("Cannot") === -1) {
          let errMsg: string;
          if (error instanceof Response) {
              const body = (error.toString().indexOf('Cannot') === -1 ?  error.json() : '') || '';
              const err = body.error || JSON.stringify(body);
              errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
          } else {
              errMsg = error.message ? error.message : error.toString();
          }
          return Observable.throw(errMsg);
      }
  }

  public setCategoryList(list){
    this.categoryList = list
  }

  public getCategoryList() {
    return this.categoryList
  }

  public setSelectProduct(product) {
    let $this = this
    $this.selectedProduct = []
    this.categoryList.map(function(obj, i){
      if(obj.catalogName == product.tagName){
        $this.selectedProduct.push(obj)
      }
    })
  }

  public getSelectProduct() {
    return this.selectedProduct
  }

  public setSelectType(type) {
    this.type = type.tagName
  }

  public getSelectType() {
    return this.type
  }

  public setSelectPayment(payment) {
    this.payment = payment.tagName
  }

  public getSelectPayment() {
    return this.payment
  }

  public isColorShow(){
    var isShow = true
    var list = this.getSelectProduct()
    if (list.length > 0) {
        list.map((val, index) => {
            if(this.inArray(val.catalogName, this.nonExistColorList)){
                isShow = false
            }
        })
    }
    return isShow
  }

  private inArray(val, list){
    var isIn = false

    list.forEach(element => {
        if(element === val){
            isIn = true
        }
    });
    return isIn
  }

}



