import { Component, OnInit } from '@angular/core';
import { ProductTradeListService } from '../service/product-trade-list.service';
import { CalTradeService } from '../service/cal-trade.service';

@Component({
  selector: 'app-product-trade-cal',
  templateUrl: './product-trade-cal.component.html',
  styleUrls: ['./product-trade-cal.component.scss']
})
export class ProductTradeCalComponent implements OnInit {
  public productBuy: any;
  public freeProduct: any;
  active : boolean = false

  constructor(
    private productTradeListService: ProductTradeListService,
    private calTradeService: CalTradeService
  ) { }

  ngOnInit() {
    this.productBuy = this.productTradeListService.getListProductBuy()
    
    $("#preload").css("display", "block");
    this.calTradeService.getFreeTrade().then(res => {
      $("#preload").css("display", "none");
      if(res.resultCode === "S") {
        this.freeProduct = res.listFreegoodsProduct
        console.log(this.freeProduct)
      }
    })
  }

  confirmOrder() {
    console.log("confirm")
  }

}



