import { OrderProductService } from '../service/order-product.service';
import { Component, OnInit } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Router } from '@angular/router';


import { ProductList } from './product-list.model';
@Component({
  selector: 'app-product-filter',
  templateUrl: './product-filter.component.html',
  styleUrls: ['./product-filter.component.scss']
})
export class ProductFilterComponent implements OnInit {
  private productLists:ProductList[]
  private typeLists:ProductList[]
  private payTypes:ProductList[]
  private getCategoryNamePromise:any
  private categoryShowList:any=[]
  constructor(
    private http: Http,
    private router : Router,
    private orderProductService:OrderProductService
  ) { }

  ngOnInit() {
    this.productLists = [
      {
        iconSrc:'./assets/images/icon/ic_device.svg',
        name:'Device',
        selected:true,
        tagName:'DEVICE'
      },
      {
        iconSrc:'./assets/images/icon/ic_device.svg',
        name:'Handset',
        selected:true,
        tagName:'HANDSET'
      },
      {
        iconSrc:'./assets/images/icon/ic_sim.svg',
        name:'SIM',
        selected:false,
        tagName:'SIM'
      },
      {
        iconSrc:'./assets/images/icon/ic_cashcard.svg',
        name:'Cash Card',
        selected:false,
        tagName:'CARD'
      },
      {
        iconSrc:'./assets/images/icon/ic_rom.svg',
        name:'ROM',
        selected:false,
        tagName:'ROM'
      },
      {
        iconSrc:'./assets/images/icon/ic_accessories.svg',
        name:'Accessories',
        selected:false,
        tagName:'ACCESSORIES'
      },
      {
        iconSrc:'./assets/images/icon/ic_aircard.svg',
        name:'Air Card',
        selected:false,
        tagName:'AIR CARD'

      },
      {
        iconSrc:'./assets/images/icon/ic_bulkpin.svg',
        name:'Bulk PIN',
        selected:false,
        tagName:'BULK PIN'
      },
      {
        iconSrc:'./assets/images/icon/ic_gadget_lot.svg',
        name:'Garget / IOT',
        selected:false,
        tagName:'Garget / IOT',
      },
      {
        iconSrc:'./assets/images/icon/ic_premium.svg',
        name:'Premium',
        selected:false,
        tagName:'Premium'
      },
      {
        iconSrc:'./assets/images/icon/ic_simprepaid.svg',
        name:'SIM Prepaid',
        selected:false,
        tagName:'SIM-PREPAID'
      },
      {
        iconSrc:'./assets/images/icon/ic_simpostpaid.svg',
        name:'SIM Postpaid',
        selected:false,
        tagName:'SIM-POSTPAID'
      },
      {
        iconSrc:'./assets/images/icon/ic_other.svg',
        name:'Other',
        selected:false,
        tagName:'Other'
      }
    ]

    this.typeLists = [
      {
        iconSrc:'./assets/images/icon/ic_quota.svg',
        name:'Quota',
        selected:true,
        tagName:'QUOTA'
      },
      {
        iconSrc:'./assets/images/icon/ic_free_quota.svg',
        name:'Free Quota',
        selected:false,
        tagName:'FREEQUOTA'
      },
      {
        iconSrc:'./assets/images/icon/ic_trade.svg',
        name:'Trade',
        selected:false,
        tagName:'TRADE'
      },
      {
        iconSrc:'./assets/images/icon/ic_free_goods.svg',
        name:'Free Goods',
        selected:false,
        tagName:'FREEGOODS'
      }
    ]

    this.payTypes=[
      {
        iconSrc:'./assets/images/icon/ic_cash.svg',
        name:'Cash',
        selected:true,
        tagName:'CASH'
      },
      {
        iconSrc:'./assets/images/icon/ic_creditcard.svg',
        name:'Credit card',
        selected:false,
        tagName:'CREDIT'
      }  
    ]
    this.getCategoryName()
  }

  onSelectProductItem(productList){
    this.clearSelected(this.productLists)
    this.orderProductService.setSelectProduct(productList)
    productList.selected = true
  }
  onSelectTypeList(typeList){
    this.clearSelected(this.typeLists)
    this.orderProductService.setSelectType(typeList)    
    typeList.selected = true
  }
  onSelectPayType(payType){
    this.clearSelected(this.payTypes)
    this.orderProductService.setSelectPayment(payType)    
    payType.selected = true
  }
  clearSelected(data){
    data.map((data=>{if(data.selected===true) {
      data.selected=false
    }}))
  }

  getCategoryName(){
    this.getCategoryNamePromise = this.orderProductService.getCategoryName();
    this.getCategoryNamePromise.then((res: any) => {
      this.orderProductService.setCategoryList(res.listCatalog)
      res.listCatalog.map(resData=>{
        if(this.categoryShowList.filter(data=>resData.catalogName===data).length===0) {
          this.categoryShowList.push(resData.catalogName)
        }
      })
    }).catch((e: any) => {
     console.log(e)
    }).then(()=>{
      this.categoryShowList = this.filterToShow(this.categoryShowList,this.productLists)
      this.orderProductService.setSelectType(this.typeLists[0]) 
      this.orderProductService.setSelectPayment(this.payTypes[0]) 
    })
  }

  filterToShow(dataToShow,dataAll){
    const resObj=[]
    dataToShow.map(subDataShow=>
      dataAll.map(subDataAll=> {if(subDataAll.tagName===subDataShow){
       resObj.push(subDataAll)
    }})) 
    resObj[0].selected=true
    this.orderProductService.setSelectProduct(resObj[0])
    return resObj 
  }

  onSubmit(){
    let type = this.orderProductService.getSelectType()

    if (type === 'TRADE') {
      this.router.navigate(['/order-product/promotion'])
    } else {
      this.router.navigate(['/order-product/product-list'])
    }
  }
  
}



