import { Component, OnInit } from '@angular/core';
import { Promotion } from './promotion.model';
import { Router } from '@angular/router';
import { Http } from '@angular/http';
import { PromotionService } from '../service/promotion.service';


@Component({
  selector: 'app-promotion',
  templateUrl: './promotion.component.html',
  styleUrls: ['./promotion.component.scss']
})
export class PromotionComponent implements OnInit {
  private promotions:Promotion[]
  private getPromotionPromise:any
  public promotionExist: boolean = true

  constructor(
    private http: Http,
    private router : Router,
    private promotionService: PromotionService
  ) { }

  ngOnInit() {
    $("#preload").css("display", "block");
    this.getPromotionPromise = this.promotionService.getPromotionList();
    this.getPromotionPromise.then((res: any) => {
      $("#preload").css("display", "none");
      if (res.resultCode == "S"){
        this.promotionExist = true
        this.promotions = res.listCatalogTrade

        if (this.promotions.length == 0){
          this.promotionExist = false
        } else {
          this.promotionService.setPromotionList(this.promotions)
        }
      } else {
        this.promotionExist = false
        this.promotions = []
      }
    }).catch((e: any) => {
     console.log(e)
    })
  }

  onClickPromotion(promotionId){
    this.promotionService.setSelectedPromotion(promotionId)
    this.router.navigate(['/order-product/product-trade']);
  }

}



