import { Component, OnInit,Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-product-card',
  templateUrl: './product-card.component.html',
  styleUrls: ['./product-card.component.scss']
})
export class ProductCardComponent implements OnInit {
  @Input() product: any;
  @Input() productName: string;
  @Input() productPrice: string;
  @Input() productItems: any;
  @Output() clickProduct = new EventEmitter()
  @Input() colorShow: boolean = false
  @Output() selectProductTrade: EventEmitter<any> = new EventEmitter<any>()

  constructor() {
  }

  ngOnInit() {
  }

  onClickProductDetail(product){
    this.clickProduct.emit(product)
  }

  isProductPromotion(){
    var result = false
    this.productItems.map((val, index)=> {
      if(typeof(val.tradeFlg) != "undefined" && val.tradeFlg == 'Y'){
        result = true
      }
    })
    return result
  }

  checkProductPromotionFlag(flag:string){
    var result = false
    this.productItems.map((val, index)=> {
      if(typeof(val.tradeFlg) != "undefined" && val.tradeFlg == flag){
        result = true
      }
    })
    return result
  }

  onItemSelect($event) {
    this.selectProductTrade.emit($event)
  }
}



