import { Component, OnInit,Input } from '@angular/core';

@Component({
  selector: 'app-product-cart-detail-item',
  templateUrl: './product-cart-detail-item.component.html',
  styleUrls: ['./product-cart-detail-item.component.scss']
})
export class ProductCartDetailItemComponent implements OnInit {
  @Input() item: any
  @Input() cart: boolean;
  public qty: number = 0
  public endDate: string = "-"

  constructor() {
  }

  ngOnInit() {
  }

  onIncrement() {
    let qty = this.qty
    if (this.item.quotaFlg == "N"){
      this.qty = this.qty + this.item.stepQty
    } else {
      qty = qty + this.item.stepQty
      if (qty > this.item.stockQty){
        qty = this.item.stockQty
        this.qty = this.item.stockQty
      } else{
        this.qty = qty
      }
    }
  }

  onDecrement() {
    let qty = this.qty

    qty = qty - this.item.stepQty
    if(qty < 0){
      qty = 0
      this.qty = 0
    } else {
      this.qty = qty
    }
  }

  selectFreeGoods(promotionFreeGoods){
    promotionFreeGoods.selected = !promotionFreeGoods.selected;
  }

  onDelete() {
    
  }

}



