import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ProductItemService } from '../../service/product-item.service';

@Component({
  selector: 'app-product-card-item',
  templateUrl: './product-card-item.component.html',
  styleUrls: ['./product-card-item.component.scss']
})
export class ProductCardItemComponent implements OnInit {
  @Input() item: any
  @Input() step: number = 1;
  @Input() quotaFlag: string;
  @Input() quotaQty: number;
  @Input() stockQty: number = 1;
  @Output() changeQtyEvent: EventEmitter<any> = new EventEmitter();
  @Input() qty: number = 0
  @Input() color: string = 'RED'
  @Input() endDate: string = "-"
  @Input() colorShow: boolean = false
  
  public colorHex: string = '#FF0000'
  public quotaString = 'Free'
  constructor(
    private productItem: ProductItemService
  ) {

  }

  ngOnInit() {
    if(this.color != undefined){
      this.colorHex = this.productItem.colourNameToHex(this.color)
    }

    if (this.quotaFlag === "Y"){
      this.quotaString = this.quotaQty.toString()
    }

    if(this.endDate === undefined){
      this.endDate = "-"
    }
  }

  onIncrement() {
    let qty = this.qty
    if (this.quotaFlag == "N"){
      this.qty = this.qty + this.step
    } else {
      qty = qty + this.step
      if (qty > this.stockQty){
        qty = this.stockQty
        this.qty = this.stockQty
      } else{
        this.qty = qty
      }
    }
    this.item.buyQty = this.qty
    this.changeQtyEvent.emit(this.item)
  }

  onDecrement() {
    let qty = this.qty

    qty = qty - this.step
    if(qty < 0){
      qty = 0 
      this.qty = 0
    } else {
      this.qty = qty
    }
    this.item.buyQty = this.qty
    this.changeQtyEvent.emit(this.item)
  }

}



