import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { OrderProductComponent } from './order-product.component'
import {PromotionComponent} from './promotion/promotion.component'
import {ProductFilterComponent} from './product-filter/product-filter.component'
import { ProductListComponent } from './product-list/product-list.component';
import { ProductDetailComponent } from './product-detail/product-detail.component';
import { ProductTradeComponent } from './product-trade/product-trade.component';
import { ProductTradeCalComponent } from './product-trade-cal/product-trade-cal.component';
import { PromotionProductComponent } from './promotion-product/promotion-product.component';
const orderProductRoutes: Routes = [ 
  {
    path: '', component: OrderProductComponent,
    children: [
      {
        path: '', component: ProductFilterComponent,
        data: {
              topbar: {
                urlBackLink: '/index',
                internal: false,
                headerText: 'สั่งซื้อสินค้า',
                backlink : true,
                cartLink : false,
                alert: {}
              }
            }
      },
      {
        path: 'promotion', component: PromotionComponent,
        data: {
              topbar: {
                urlBackLink: '/order-product',
                internal: false,
                headerText: 'สั่งซื้อสินค้า',
                backlink : true,
                cartLink : false,
                alert: {
                  show: false,
                }
              }
            }
      },
      {
        path: 'promotion-product', component: PromotionProductComponent,
        data: {
              topbar: {
                urlBackLink: '/order-product/product-list',
                internal: false,
                headerText: 'สั่งซื้อสินค้า',
                backlink : true,
                cartLink : false,
                alert: {
                  show: false,
                }
              }
            }
      },
      {
        path: 'product-list', component: ProductListComponent,
        data: {
              topbar: {
                urlBackLink: '/order-product',
                internal: false,
                headerText: 'สั่งซื้อสินค้า',
                backlink : true,
                cartLink : true,
                alert: {
                  show: false,
                }
              }
            }
      },
      {
        path: 'product-detail', component: ProductDetailComponent,
        data: {
              topbar: {
                urlBackLink: '/order-product/product-list',
                internal: false,
                headerText: 'สั่งซื้อสินค้า',
                backlink : true,
                cartLink : true,
                alert: {
                  show: false,
                }
              }
            }
      },
      {
        path: 'product-trade', component: ProductTradeComponent,
        data: {
              topbar: {
                urlBackLink: '/order-product/promotion',
                internal: false,
                headerText: 'สั่งซื้อสินค้า',
                backlink : true,
                cartLink : true,
                alert: {
                  show: false,
                }
              }
            }
      },
      {
        path: 'product-trade-cal', component: ProductTradeCalComponent,
        data: {
              topbar: {
                urlBackLink: '/order-product/product-trade',
                internal: false,
                headerText: 'สั่งซื้อสินค้า',
                backlink : true,
                cartLink : true,
                alert: {
                  show: true,
                  message: "ยกเลิกการทำรายการ",
                  showCancel: true,
                }
              }
            }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(orderProductRoutes)],
  exports: [RouterModule]
})

export class OrderProductRoutingModule {
}



